* {
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        margin: 0;
        padding: 0;
        font-size: 15px;
        position: relative;
        font-family: "Spartan";
        font-style: normal;
        font-weight: 500;
}

div {
        display: flex;
}

.header-top {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        height: 156px;
        background-image: url(/public/images/bg-header-desktop.svg);
        background-color:hsl(180, 29%, 50%); ;
}
.main-background {
        background-color:hsl(180, 52%, 96%);
        align-self: stretch;
        flex-grow: 1;
        flex-direction: column;
        justify-content: start;
        align-items: center;
        margin-top: 156px;
        padding: 70px 10% 100px 10%;
}
.column {
        flex-direction: column;
        align-items: flex-start;
}
.ai-c {
        align-items: center;
}
.row {
        flex-direction: row;
}
.card-info {
        margin-left: 30px;
        align-self: center;
}
.NEW {
        color: white;
        font-size: 13px;
        font-weight: 700;  
        background-color: hsl(180, 29%, 50%);
        padding: 7px 5px 3px 5px;
        margin-right: 10px;
        border-radius: 7px;
        align-self: center;
}
.FEATURED {
        color: white;
        font-weight: 700;
        font-size: 13px; 
        background-color: hsl(180, 14%, 20%);
        padding: 7px 5px 3px 5px;
        margin-right: 10px;
        border-radius: 9px;
        align-self: center;
}
.container-filter {
        position: absolute;
        border : none;
        border-radius: 8px;
        top:120px;
        width: 80%;
        margin: 0 10%;
        padding: 15px 0;
        background-color: white;
        z-index: 1;
        justify-content: center;
}
